import React, { useEffect, useRef } from 'react';
import styled from 'styled-components';
import { srConfig } from '@config';
import sr from '@utils/sr';
import { usePrefersReducedMotion } from '@hooks';
import { Icon } from '@components/icons';
import { StaticImage } from 'gatsby-plugin-image';

const StyledSkillsSection = styled.section`
  max-width: 900px;

  .inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .skills-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    gap: 15px;
    padding: 20px 0;
    margin: 20px 0;
    width: 100%;

    @media (max-width: 768px) {
      grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    }
  }

  .skill-item {
    ${({ theme }) => theme.mixins.boxShadow};
    position: relative;
    border-radius: var(--border-radius);
    background-color: var(--light-navy);
    padding: 2rem;
    height: 100%;
    transition: var(--transition);
    cursor: pointer;

    &:hover,
    &:focus {
      transform: translateY(-5px);
    }

    .skill-image {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 60px;
      
      svg, img, .gatsby-image-wrapper {
        width: 50px;
        height: 50px;
        object-fit: contain;
        transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
      }
    }

    .skill-name {
      margin-top: 10px;
      color: var(--lightest-slate);
      font-family: var(--font-mono);
      font-size: var(--fz-xs);
      text-align: center;
    }

    &:hover {
      .skill-image svg,
      .skill-image img,
      .skill-image .gatsby-image-wrapper {
        transform: scale(1.15);
      }
    }
  }
`;

const skills = [
  {
    name: 'Java',
    icon: 'Java',
    isImage: false,
    url: 'https://www.java.com/'
  },
  {
    name: 'Python',
    icon: 'Python',
    isImage: false,
    url: 'https://www.python.org/'
  },
  {
    name: 'JavaScript',
    icon: 'JavaScript',
    isImage: false,
    url: 'https://developer.mozilla.org/en-US/docs/Web/JavaScript'
  },
  {
    name: 'React.js',
    icon: 'React',
    isImage: false,
    url: 'https://reactjs.org/'
  },
  {
    name: 'Vue.js',
    icon: 'Vue',
    isImage: false,
    url: 'https://vuejs.org/'
  },
  {
    name: 'Node.js',
    icon: 'NodeJS',
    isImage: false,
    url: 'https://nodejs.org/'
  },
  {
    name: 'Django',
    icon: '/skills/django.png',
    isImage: true,
    url: 'https://www.djangoproject.com/'
  },
  {
    name: 'Spring Boot',
    icon: '/skills/springboot.png',
    isImage: true,
    url: 'https://spring.io/projects/spring-boot'
  },
  {
    name: 'Symfony',
    icon: '/skills/symfony.png',
    isImage: true,
    url: 'https://symfony.com/'
  }
];

const Skills = () => {
  const revealTitle = useRef(null);
  const revealSkills = useRef([]);
  const prefersReducedMotion = usePrefersReducedMotion();

  useEffect(() => {
    if (prefersReducedMotion) {
      return;
    }

    sr.reveal(revealTitle.current, srConfig());
    revealSkills.current.forEach((ref, i) => sr.reveal(ref, srConfig(i * 100)));
  }, []);

  return (
    <StyledSkillsSection id="skills">
      <div className="inner">
        <h2 className="numbered-heading" ref={revealTitle}>
          Skills
        </h2>

        <div className="skills-grid">
          {skills.map(({ name, icon, isImage, url }, i) => (
            <a
              key={i}
              className="skill-item"
              href={url}
              target="_blank"
              rel="noopener noreferrer"
              ref={el => (revealSkills.current[i] = el)}>
              <div className="skill-image">
                {isImage ? (
                  name === 'Django' ? (
                    <StaticImage
                      src="../../images/skills/django.png"
                      alt="Django"
                      width={50}
                      height={50}
                      placeholder="none"
                    />
                  ) : name === 'Spring Boot' ? (
                    <StaticImage
                      src="../../images/skills/springboot.png"
                      alt="Spring Boot"
                      width={50}
                      height={50}
                      placeholder="none"
                    />
                  ) : name === 'Symfony' ? (
                    <StaticImage
                      src="../../images/skills/symfony.png"
                      alt="Symfony"
                      width={50}
                      height={50}
                      placeholder="none"
                    />
                  ) : null
                ) : (
                  <Icon name={icon} />
                )}
              </div>
              <div className="skill-name">{name}</div>
            </a>
          ))}
        </div>
      </div>
    </StyledSkillsSection>
  );
};

export default Skills;